<template>
    <div>
        <div class="container text-left">
			<form class="pl-7 pr-7 form form-group">
				<div class="form-group">
					<label for="btitle">제목</label> <input type="text" class="form-control" 
                    v-model="curQuestion.btitle" readonly>
				</div>
				<div class="form-group row">
					<div class="col-sm-4">
						<label for="userid">작성자 </label> <input type="text" 
							class="form-control" v-model="curQuestion.userid" readonly>
					</div>
					<div class="col-sm-4">
						<label for="bwritedate">작성날짜</label> <input type="text"
							class="form-control" v-model="curQuestion.bwritedate" readonly>
					</div>
					<div class="col-sm-4">
						<label for="breadcnt">조회수</label> <input type="text"
							class="form-control" v-model="curQuestion.breadcnt" readonly>
					</div>
				</div>
				<div class="form-group">
					<label for="bcontent">내용</label>
					<textarea rows="10" style="height: 100%;" class="form-control" v-model="curQuestion.bcontent" readonly></textarea>
				</div>
				<!--footer-->
				<div class="container row justify-content-center"
					style="margin-bottom: 0">
					<!-- <c:if test="${loginInfo.userid eq dto.userid}"> -->
						<!-- 세션아이디랑 글쓴이가 같을때만 보여줌 -->
						<input type="button" class="btn btn-outline-primary mr-1"
							value="수정하기" @click='gotoModify'>
						<input type="button" class="btn btn-outline-danger mr-1"
							value="삭제하기" @click='gotoDelete'>
					<!-- </c:if> -->
					<input type="button" class="btn btn-outline-secondary" value="목록"
						@click='gotoList' >
				</div>
			</form>
		</div>
        <br><hr><br>
        <div class="container bootdey">
			<div class="col-md-14 bootstrap snippets">
				<div class="panel">
					<div class="panel-body">
						<h5>전체 답변</h5>
						<br>
						<div id="cmtList">
							<!-- 댓글  -->
							<li class="media-block" v-for="reply in replyList" :key="reply.bnum">
                                <span class="media-right sm-1">
                                    <img class="img-circle img-sm" alt="Profile Picture" src="https://bootdey.com/img/Content/avatar/avatar1.png">
                                </span>
								<div class="media-body">
									<div class="mar-btm">
										<span class="btn-link text-semibold media-heading box-inline">  {{reply.userid}}</span>
										<p class="text-muted text-sm">{{reply.bwritedate}}</p>
									</div>
									<p>{{reply.bcontent}}</p>
								</div>
							</li>
							<!-- 댓글 -->
						</div>
					</div>
				</div>
				<div class="panel">
					<div class="panel-body">
						<textarea id="cmt" class="form-control" rows="2"
							placeholder="답변을 입력하세요" v-model="newReply.bcontent" @keyup.enter="addComment"></textarea>
                        <br>
						<div class="mar-top clearfix">
							<button id="cmtBtn" @click="addComment"
								class="btn btn-sm btn-outline-primary pull-right justify-content-end">
								답변 등록
							</button>
						</div>
					</div>
				</div>
                <br><br>
			</div>
		</div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            curQuestion:{
                bnum : '',
                btitle : '',
                userid : '',
                breadcnt : '',
                bwritedate : '',
                bcontent : '',
                questionnum : '',
            },
            replyList:[],
            newReply:{
                questionnum : '',
                bcontent: '',
            },
        }
    },
    created() {
        this.curQuestion.bnum = this.$route.params.bnum;
        axios.get("http://localhost:7777/happyhouse/qna/" + this.curQuestion.bnum)
        .then((response) => {
            this.curQuestion = response.data;
        })
        .catch((ex) => {console.log(ex)});
        
        this.getComment();
    }, 
    methods: {
        gotoModify(){
            this.$router.push({name : 'Modify', params : {question : this.curQuestion}});
        },
        gotoList(){
            this.$router.push("/happyhouse/qna");
        },
        gotoDelete(){
            if(confirm("삭제하시겠습니까?")){
                axios.delete("http://localhost:7777/happyhouse/qna/" + this.curQuestion.bnum)
                .then(() => {
                    this.$router.push("/happyhouse/qna");
                })
                .catch((ex) => {console.log(ex)});
            }
        },
        addComment(){
            if(this.newReplyContent == ''){
                alert("답변 내용을 입력하세요.");
            }else{
                this.newReply.questionnum = this.curQuestion.bnum;
                axios.post("http://localhost:7777/happyhouse/qna/ans", this.newReply)
                .then(() => {
                    this.getComment();
                    this.newReply.bcontent = '';
                })
                .catch((ex) => {console.log(ex)});
            }
        },
        getComment(){
            axios.get("http://localhost:7777/happyhouse/qna/ans", this.curQuestion.bnum)
            .then((response) => {
                this.replyList = response.data;
            })
            .catch((ex) => {console.log(ex)});

            console.log(this.replyList);
        }
    }
}
</script>